@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&family=PT+Sans&display=swap);

.header-select-city {
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
  align-items: center;
  font-weight: bold;
}
.header-select-city img {
  padding-left: 14px;
}
.header-form-search-panel {
  width: 60px;
  height: 60px;
  background-color: #313030;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
}
.header {
  width: 100%;
  height: 140px;
  margin-top: 30px;
  background-color: #F2F2F2;
}
.header-filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 60px;
}
.header-filters-title {
  font-weight: 400;
  font-size: 36px;
}
.header-form{
  display: flex;
  justify-content: space-between;
}

.header-form-filter-panel {
  width: 60px;
  height: 60px;
  background-color: #313030;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
}
.restaurant-container__card {
  background: #313030;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  cursor: pointer;
}
.card-list-item-img {
  width: 90%;
  height: 60%;
}
.restaurant_container__card-naming {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  font-size: 18px;
  font-weight: 800;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0 0 30px 0;
}
.restaurant_container__card-discription {
  display: flex;
  width: 90%;
  justify-content: space-between;
}
.restaurant_container__card-discription p{
  font-size: 16px;
  font-weight: 600;
}
.restaurant_container_card__discription-contact {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.restaurant_container_card__discription-contact img{
  width: 97px;
  height: 16px;
  margin-top: 10px;
}
.restaurant_container_card__discription-price{
 display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.restaurant_container_card__discription-price img{
  width: 77.13px;
  height: 17.06px;
  margin-top: 10px;
}
.restaurant_container__card-discription p, .card-list-item-title{
  margin-top: 10px;
}
.main-card-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 2.5%;
  grid-row-gap: 35px;
  margin-top: 20px;
}
@media (max-width: 1600px) {
  .main-card-list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 1100px) {
  .main-card-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 769px) {
  .main-card-list {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 15px;
  }
}
.main p{
  font-family: 'Montserrat', sans-serif;
  color: white;
}
.main {
  height: 80vh;
  margin-top: 10px;
  overflow: hidden;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  width: 5px; 
  background-color: #F2F2F2;
}
::-webkit-scrollbar-thumb {
  background-color: #F2F2F2;
  border-radius: 10px;
}
.sidebar {
  width: 130px;
  height: 100vh;
  background-color: #313030;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.logo {
  margin: 25px 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.sidebar-list {
  width: 90%;
  height: 100%;
  align-self: end;
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.sidebar-list-items {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;  
}
.sidebar-list-item {
  margin: 35px 0 35px 0;
}
.sidebar-list-wrapper {
  height: 95px;
  background-color: #F2F2F2;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  position: relative;
  right: -1px;
  top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.after, .before {
  position: absolute;
  width: 20%;
  height: 21%;
  right: 0;
  background-color: #F2F2F2;
}
.after {
  top: -20%;
}
.before {
  top: 99%;  
}
.after::after, .before::before {
  position: absolute;
  content: "";
  background-color: #313030;
  width: 100%;
  height: 100%;
  left: -1px;
}
.after::after {
  border-bottom-right-radius: 15px;
  top: -1px;
}
.before::before {
  border-top-right-radius: 15px;
  top: 1px;
}
.sidebar-list__btn {
  width: 70%;
  height: 70%;
  background: linear-gradient(to bottom right, #FEC163, #DE4313);
  border-radius: 15px;
}
html, body {
  margin: 0;
  padding: 0;
  background-color: #F2F2F2;
  font-family: 'PT Sans', sans-serif;
}
h1, h2, h3, p {
  font-family: 'PT Sans', sans-serif;
  margin: 0;
}
.app {
  display: flex;
}
.content-page {
  margin: 0 40px;
  width: 100%;
  height: 100vh;
}
